
import BScroll from "better-scroll";
import { onMounted, nextTick, ref, watchEffect } from "vue";

export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1,
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true,
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    listenScroll: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    scrollData: {
      type: Array,
      default: null,
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false,
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false,
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 20,
    },
  },
  setup(props: any, setupContext: any) {
    const wrapper = ref(null);
    const initScroll = () => {
      if (!wrapper.value) {
        return;
      }

      const scroll = new BScroll(wrapper.value, {
        probeType: props.probeType,
        click: props.click,
        scrollX: props.scrollX,
      });

      // 
      if (props.listenScroll) {
        scroll.on("scroll", (pos: number) => {
          setupContext.emit("scroll", pos);
        });
      }
      // 
      if (props.pullup) {
        scroll.on("scrollEnd", () => {
          // 
          if (scroll.y <= scroll.maxScrollY + 50) {
            setupContext.emit("scrollToEnd");
          }
        });
      }
      // 
      if (props.pulldown) {
        scroll.on("touchend", (pos: any): void => {
          // 
          if (pos.y > 50) {
            setupContext.emit("pulldown");
          }
        });
      }
      // 
      if (props.beforeScroll) {
        scroll.on("beforeScrollStart", () => {
          setupContext.emit("beforeScroll");
        });
      }
    };

    // const disable = () => {
    //   // better-scrolldisable
    //   scroll?.disable();
    // };

    // const enable = () => {
    //   // better-scrollenable
    //   scroll?.enable();
    // };

    // const refresh = () => {
    //   // better-scrollrefresh
    //   scroll?.refresh();
    // };

    // const scrollTo = () => {
    //   // better-scrollscrollTo
    //   scroll?.scrollTo.apply(scroll, arguments);
    // };

    // const scrollToElement = () => {
    //   // better-scrollscrollToElement
    //   scroll?.scrollToElement.apply(scroll, arguments);
    // };

    onMounted(() => {
      nextTick(() => {
        initScroll();
      });
    });
    return {};
  },
};
